import { Box, Text, Center, Button, VStack } from "@chakra-ui/react";
import React from "react";
import { agreementStore } from "../store/agreementStore";
import { entityStore } from "../store/entityStore"
import "./styles.css"

export default function PdfDocument() {

    const copyOfAgreement = agreementStore(state => state.copyOfAgreement)

    const lessee = entityStore(state => state.lessee)
    const address = entityStore(state => state.address)
    const lessor = entityStore(state => state.lessor)
    const date = entityStore(state => state.date)
    const amount = entityStore(state => state.amount)

    return (
        <VStack>
            <Box overflowY={"scroll"} height="800px" display={"flex"} justifyContent="center" userSelect={"none"}>
                <Box id="pdf" border={"2px solid black"} width={"70%"} padding={"20px"} margin={"5px"}>
                    <Box>
                        <Center>
                            <b>
                                RESIDENTIAL RENTAL AGREEMENT
                            </b>
                        </Center>
                    </Box>
                    <Box>
                        <Text > THIS LEASE (the "Lease") dated this _____ day of _____________, 20____ <br /><br /></Text>
                        <Text >BETWEEN</Text>
                        <Text>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; <span>{copyOfAgreement["clauses"][0]["branches"][0]["questions"][0]["metadata"]["answer_template"].replace("$",
                            copyOfAgreement["clauses"][0]["branches"][0]["questions"][0]["user_selected"])}
                            <br /><br /><br />
                        </span></Text>
                    </Box>
                    <Box>
                        <Center>_________________________</Center>
                        <Center>(the "Landlord")</Center>
                        <Center>- AND-</Center>
                        <Center>_________________________</Center>
                        <Center>(the "Tenant") <br /><br /></Center>
                        <Center>(individually the “Party” and collectively the “Parties”) <br /><br /></Center>
                    </Box>

                    <Box >
                        <Text >
                            <b>IN CONSIDERATION OF</b> the Landlord leasing certain premises to the Tenant and other valuable consideration, the receipt and sufficiency of which consideration is hereby acknowledged, the Parties agree as follows:
                            <br />
                            <br />
                        </Text>
                    </Box>

                    <Box>
                        <b><u>Leased Property</u></b>
                        <ol>
                            <li>The Landlord agrees to rent to the Tenant the house, municipally described as ______________________________________________ (the "Property"), for use as residential premises only<br /></li>
                            <li>No guests of the Tenants may occupy the Property for longer than one week without the prior written permission of the Landlord.<br /></li>
                            <li>No pets or animals are allowed to be kept in or about the Property without the prior written permission of the Landlord. Upon 30 days' notice, the Landlord may revoke any permission previously given pursuant to this clause.<br /></li>
                            <li>The Tenant and members of Tenant's household will not smoke anywhere in the Property nor permit any guests or visitors to smoke in the Property.<br /></li>
                            <li>The Property is provided to the Tenant without any furnishings.<br /><br /></li>

                            <b><u>Term</u></b>

                            <li>The term of the Lease commences at 12:00 noon on 12 July 2022 and ends at 12:00 noon on 12 July 2022.<br /></li>
                            <li>Upon any notice required under the Act, or no notice where there is no Act or the Act does not specify, the Landlord may terminate this tenancy where the Tenant has breached any provision of this rental, including the failure to pay the Rent for at least one month. <br /><br /></li>

                            <b><u>Rent</u></b>

                            <li>Subject to the provisions of this Lease, the rent for the Property is ₹___________per {copyOfAgreement["clauses"][3]["branches"][0]["questions"][0]["metadata"]["answer_template"].replace("$",
                            copyOfAgreement["clauses"][3]["branches"][0]["questions"][0]["user_selected"])} (the "Rent").<br /></li>
                            <li>The Tenant will pay the Rent on or before the ____________________ of each and every month of the term of this Lease to the Landlord at ______________________ or at such other place as the Landlord may later designate by cash or cheque.<br /><br /></li>

                            <b><u>Quiet Enjoyment</u></b>

                            <li>The Landlord covenants that on paying the Rent and performing the covenants contained in this Lease, the Tenant will peacefully and quietly have, hold, and enjoy the Property for the agreed term.<br /><br /></li>

                            <b><u>Tenant Improvement</u></b>

                            <li>The Tenant will obtain written permission from the Landlord before doing any of the following: <br /></li>
                            <ol style={{listStyle: "lower-alpha"}}>
                                <li>applying adhesive materials, or inserting nails or hooks in walls or ceilings other than two small picture hooks per wall;<br /></li>
                                <li>painting, wallpapering, redecorating or in any way significantly altering the appearance of the Property;<br /></li>
                                <li>removing or adding walls, or performing any structural alterations;<br /></li>
                                <li>changing the amount of heat or power normally used on the Property as well as installing additional electrical wiring or heating units;<br /></li>
                                <li>placing or exposing or allowing to be placed or exposed anywhere inside or outside the Property any placard, notice or sign for advertising or any other purpose; or<br /></li>
                                <li>affixing to or erecting upon or near the Property any radio or TV antenna or tower.<br /><br /></li>
                            </ol>

                            <b><u>Attorney Fees</u></b>
                            <li>In the event that any action is filed in relation to this Lease, the unsuccessful Party in the action will pay to the successful Party, in addition to all the sums that either Party may be called on to pay, a reasonable sum for the successful Party's attorney fees.<br /> <br /></li>
                            
                            <b><u>Governing Law</u></b>
                            <li>This Lease will be construed in accordance with, and exclusively governed by, the laws of Uttar Pradesh.<br /><br /></li>

                            <b><u>Severability</u></b>
                            <li>If there is a conflict between any provision of this Lease and the applicable legislation of Uttar Pradesh (the "Act"), the Act will prevail and such provisions of the Lease will be amended or deleted as necessary in order to comply with the Act. Further, any provisions that are required by the Act are incorporated into this Lease. <br /></li>
                            <li>The invalidity or unenforceability of any provisions of this Lease will not affect the validity or enforceability of any other provision of this Lease. Such other provisions remain in full force and effect.<br /><br /></li>

                            <b><u>Amendment of Lease</u></b>
                            <li>This Lease may only be amended or modified by a written document executed by the Parties.<br /><br /></li>

                            <b><u>Assignment and Subletting</u></b>
                            <li>Without the prior, express, and written consent of the Landlord, the Tenant will not assign this Lease, or sublet or grant any concession or licence to use the Property or any part of the Property. A consent by the Landlord to one assignment, subletting, concession, or licence will not be deemed to be a consent to any subsequent assignment, subletting, concession, or licence. Any assignment, subletting, concession, or licence without the prior written consent of the Landlord, or an assignment or subletting by operation of law, will be void and will, at the Landlord's option, terminate this Lease.<br /><br /></li>

                            <b><u>Maintenance</u></b>
                            <li>The Tenant will, at its sole expense, keep and maintain the Property and appurtenances in good and sanitary condition and repair during the term of this Lease and any renewal of this Lease.<br /></li>
                            <li>Major maintenance and repair of the Property not due to the Tenant's misuse, waste, or neglect or that of the Tenant's employee, family, agent, or visitor, will be the responsibility of the Landlord or the Landlord's assigns.<br /><br /></li>

                            <b><u>Care and Use of Property</u></b>
                            <li>The Tenant will promptly notify the Landlord of any damage, or of any situation that may significantly interfere with the normal use of the Property or to any furnishings supplied by the Landlord.<br /></li>
                            <li>The Tenant will not engage in any illegal trade or activity on or about the Property.<br /></li>
                            <li>The Parties will comply with standards of health, sanitation, fire, housing and safety as required by law.<br /></li>
                            <li>If the Tenant is absent from the Property and the Property is unoccupied for a period of 4 consecutive days or longer, the Tenant will arrange for regular inspection by a competent person. The Landlord will be notified in advance as to the name, address and phone number of the person doing the inspections.<br /></li>
                            <li>At the expiration of the term of this Lease, the Tenant will quit and surrender the Property in as good a state and condition as they were at the commencement of this Lease, reasonable use and wear and tear excepted.<br /></li>
                            <li>During the term of this Lease or after its termination, the Landlord may charge the Tenant for replacement of locks and/or lost keys to the Property, and any administrative fees associated with the replacement as a result of the Tenant's misplacement of the keys.<br /><br /></li>

                            <b><u>Rules and Regulations</u></b>
                            <li>The Tenant will obey all rules and regulations of the Landlord regarding the Property.<br /><br /></li>
                            
                            <b><u>Address for Notice</u></b>
                            <li>For any matter relating to this tenancy, the Tenant may be contacted at the Property or through the phone number below. After this tenancy has been terminated, the contact information of the Tenant is:<br /></li>
                            <ol style={{listStyle: "lower-alpha"}}>
                                <li>Name: _________________________<br /></li>
                                <li>Phone: _________________________<br /></li>
                                <li>Post termination notice address: ________________________________________________________<br /> <br /></li>
                            </ol>

                            <li>For any matter relating to this tenancy, whether during or after this tenancy has been terminated, the Landlord's address for notice is:</li>
                            <ol style={{listStyle: "lower-alpha"}}>
                                <li>Name: _________________________<br /></li>
                                <li>Address: ______________________</li>
                                <p style={{textAlign: "left"}}>The contact information for the Landlord is:</p>
                                <li>Phone: _________________________<br /><br /></li>
                            </ol>

                            <b><u>General Provisions</u></b>
                            <li>All monetary amounts stated or referred to in this Lease are based in the Indian rupee.<br /></li>
                            <li>Any waiver by the Landlord of any failure by the Tenant to perform or observe the provisions of this Lease will not operate as a waiver of the Landlord's rights under this Lease in respect of any subsequent defaults, breaches or non-performance and will not defeat or affect in any way the Landlord's rights in respect of any subsequent default or breach.<br /></li>
                            <li>This Lease will extend to and be binding upon and inure to the benefit of the respective heirs, executors, administrators, successors and assigns, as the case may be, of each Party. All covenants are to be construed as conditions of this Lease.<br /></li>
                            <li>All sums payable by the Tenant to the Landlord pursuant to any provision of this Lease will be deemed to be additional rent and will be recovered by the Landlord as rental arrears.<br /></li>
                            <li>Locks may not be added or changed without the prior written agreement of both Parties. <br /></li>
                            <li>Headings are inserted for the convenience of the Parties only and are not to be considered when interpreting this Lease. Words in the singular mean and include the plural and vice versa. Words in the masculine mean and include the feminine and vice versa.<br /></li>
                            <li>This Lease may be executed in counterparts. Facsimile signatures are binding and are considered to be original signatures.<br /></li>
                            <li>This Lease constitutes the entire agreement between the Parties. Any prior understanding or representation of any kind preceding the date of this Lease will not be binding on either Party except to the extent incorporated in this Lease.<br /></li>
                            <li>The Tenant will indemnify and save the Landlord, and the owner of the Property where different from the Landlord, harmless from all liabilities, fines, suits, claims, demands and actions of any kind or nature for which the Landlord will or may become liable or suffer by reason of any breach, violation or non-performance by the Tenant or by any person for whom the Tenant is responsible, of any covenant, term, or provisions hereof or by reason of any act, neglect or default on the part of the Tenant or other person for whom the Tenant is responsible. Such indemnification in respect of any such breach, violation or non-performance, damage to property, injury or death occurring during the term of the Lease will survive the termination of the Lease, notwithstanding anything in this Lease to the contrary.<br /></li>
                            <li>The Tenant agrees that the Landlord will not be liable or responsible in any way for any personal injury or death that may be suffered or sustained by the Tenant or by any person for whom the Tenant is responsible who may be on the Property of the Landlord or for any loss of or damage or injury to any property, including cars and contents thereof belonging to the Tenant or to any other person for whom the Tenant is responsible.<br /></li>
                            <li>The Tenant is responsible for any person or persons who are upon or occupying the Property or any other part of the Landlord's premises at the request of the Tenant, either express or implied, whether for the purposes of visiting the Tenant, making deliveries, repairs or attending upon the Property for any other reason. Without limiting the generality of the foregoing, the Tenant is responsible for all members of the Tenant's family, guests, servants, tradesmen, repairmen, employees, agents, invitees or other similar persons.<br /></li>
                            <li>Time is of the essence in this Lease.<br /><br /></li>
                        </ol>

                        <p><b>IN WITNESS WHEREOF</b> _________________________ and _________________________ have duly affixed their signatures on this _____ day of _____________, 20____.</p>
                        <p style={{textAlign: "right"}}>_______________________________<br/>______________________(Landlord)<br/></p>
                        <p style={{textAlign: "right"}}>_______________________________<br/>______________________(Tenant)<br/></p>
                    </Box>
                </Box>
            </Box>
            <Box position={"absolute"} top={700} fontSize={150} opacity={"0.1"} transform={"rotate(-30deg)"}>DRAFT</Box>
        </VStack>

    );
}