import './App.css';
import React from 'react';
import { Box, Flex, Progress, Button } from '@chakra-ui/react';
import { agreementData } from "./data";
import MCQuestion from "./components/agreement/mcquestion";
import TxtQuestion from './components/agreement/txtQuestion';
import { agreementStore } from "./store/agreementStore"
import PdfDocument from './components/pdfDocument';
import Nav from './components/Nav';
import { useNavigate } from "react-router-dom";

function App() {
  const navigate = useNavigate();
  let clauseToQuestionArray = {};
  for (let i = 0; i < agreementData["clauses"].length; ++i) {
    clauseToQuestionArray[agreementData["clauses"][i]["metadata"]["name"]] =
      agreementData["clauses"][i]["branches"][0]["questions"]
  }
  const [isPrint, setIsPrint] = React.useState(false);
  const [clause, setClause] = React.useState("General");
  const [progressVal, setProgressVal] = React.useState(15);
  const [save, setSave] = React.useState({
    array_clause_name: ["General", "Property", "Parties", "Terms", "Final details"],
    progress_estimate: [15, 25, 38, 50, 65],
    curr_clause_idx: 0
  });

  const handleSave = () => {
    const array_clause_name = save["array_clause_name"];
    const curr_clause_idx = save["curr_clause_idx"];
    const progress_estimate = save["progress_estimate"]; // To Do: Automatically Compute
    
    if(curr_clause_idx < array_clause_name.length - 1) {
      setClause(array_clause_name[curr_clause_idx+1]);
      setProgressVal(progress_estimate[curr_clause_idx + 1]);
      setIsPrint(false);
      setSave({array_clause_name: array_clause_name, curr_clause_idx: curr_clause_idx+1, progress_estimate: progress_estimate})
    }
    else {
      setIsPrint(true);
      setProgressVal(100);
    }
  }
  const setCopyOfAgreement = agreementStore(state => state.setCopyOfAgreement);
  React.useEffect(() => {
    setCopyOfAgreement(JSON.parse(JSON.stringify(agreementData)))
  }, [])
  return (
    <>
    <Nav />
      <Flex height="5vh" width="100vw" backgroundColor="ffff" justify={"space-between"}>
        <Box>LegalNinja</Box>
        <Box marginEnd={10} fontWeight={"bold"} cursor={"pointer"} onClick={() => navigate("/login")}>Login</Box>
      </Flex>
      <Flex height="40vh" width="100vw" backgroundColor="#555966" padding="20px" flexDir="column">
        <Box fontSize="30" color="white">RESIDENTIAL RENTAL AGREEMENT</Box>
        <Flex flexWrap={"wrap"} gap="30"  fontSize="20" padding="20px" color="white">
          {/* Todo Hardcoded*/}
          <Box cursor={"pointer"} onClick={() => { setClause("General"); setSave(prev => Object.assign( prev, {curr_clause_idx: 0})); setIsPrint(false); setProgressVal(15); }}>General</Box> 
          <Box cursor={"pointer"} onClick={() => { setClause("Property"); setSave(prev => Object.assign( prev, {curr_clause_idx: 1})); setIsPrint(false); setProgressVal(25); }}>Property</Box>
          <Box cursor={"pointer"} onClick={() => { setClause("Parties"); setSave(prev => Object.assign( prev, {curr_clause_idx: 2})); setIsPrint(false); setProgressVal(38); }}>Parties</Box>
          <Box cursor={"pointer"} onClick={() => { setClause("Terms"); setSave(prev => Object.assign( prev, {curr_clause_idx: 3})); setIsPrint(false); setProgressVal(50); }}>Terms</Box>
          <Box cursor={"pointer"} onClick={() => { setClause("Final details"); setSave(prev => Object.assign( prev, {curr_clause_idx: 4})); setIsPrint(false); setProgressVal(65); }}>Final Details</Box>
          <Box>Signing</Box>
          <Box cursor={"pointer"} onClick={() => {setIsPrint(prevState => !prevState); setProgressVal(100);}} >Print/Download</Box>
        </Flex>
        <Progress width="62%" colorScheme={"green"} value={progressVal} />
      </Flex>

      <Box>
        {/* <MCQuestion jsonData={agreementData} id={"c0_b0_q1"} /> */}
        {
          isPrint === true ? <Box>
            <PdfDocument />
          </Box> :
            <TxtQuestion questionData={clauseToQuestionArray[clause]} />
        }
      </Box>
      <Button onClick={() => handleSave()} colorScheme={"green"} width={"120px"} margin={5} padding={1}>
      {
        save["curr_clause_idx"] === save["array_clause_name"].length -1 ? "Purchase": "Save & Next"
      }
      </Button>
    </>
  );
}

export default App;
